import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { TabnavPage } from './tabnav.page';

const routes: Routes = [
  {
    path: 'tab-nav',
    component: TabnavPage,
    children: [
      {
        path: 'activities',
        loadChildren: () => import('./../activities/activities.module').then( m => m.ActivitiesPageModule)
      },
      {
        path: 'activity-details/:event_id',
        loadChildren: () => import('./../activity-details/activity-details.module').then( m => m.ActivityDetailsPageModule)
      },
      {
        path: 'activity-subscribe',
        loadChildren: () => import('./../activity-subscribe/activity-subscribe.module').then( m => m.ActivitySubscribePageModule)
      },
      {
        path: 'activity-booking',
        loadChildren: () => import('./../activity-booking/activity-booking.module').then( m => m.ActivityBookingPageModule)
      },
      {
        path: 'activity-parts',
        loadChildren: () => import('./../activity-parts/activity-parts.module').then( m => m.ActivityPartsPageModule)
      },
      {
        path: 'about',
        loadChildren: () => import('./../about/about.module').then( m => m.AboutPageModule)
      }
    ]
  },
  {
    path: '',
    redirectTo: '/tab-nav/activities'
  }
];


@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class TabnavPageRoutingModule {}
